<template>
    <Title :="belovedTitle"></Title>
    <div id="Beloved">
        <div id="bg"></div>
        <img id="pyz" src="../assets/beloved/pyz.png" alt="pyz" />
        <div id="heart"></div>
        <img id="zyq" src="../assets/beloved/zyq.png" alt="zyq" />
    </div>
    <Poem :content="belovedPoem"></Poem>
    <div id="timeline">
        <div id="Encryption" style="display: none">
            <el-button type="danger" icon="Lock" round @click="timeline.encryption = !timeline.encryption">更多</el-button>
        </div>
        <el-timeline>
            <el-timeline-item v-for="(activity, index) in computed_acticities" :key="index" :icon="activity.icon"
                :type="activity.type" :color="activity.color" :size="activity.size" :timestamp="activity.timestamp"
                placement="top">
                <div v-html="activity.content"></div>
            </el-timeline-item>
        </el-timeline>
    </div>
</template>

<script setup>
import { useStore } from 'vuex';
import Title from './Utils/Title.vue';
import Poem from './Utils/Poem.vue';
import { reactive, computed } from 'vue';
const store = useStore()
// Beloved Poem
var d = new Date(store.state.startDate.dateString);
var day = (new Date().getTime() - d.getTime()) / (24 * 60 * 60 * 1000);
var time = Math.floor(day);
const belovedPoem = {
    title: "Beloved",
    sentence: `我们已经在一起${time}天了`,
    derive: store.state.startDate.dateString,
    lines: [
        "👻春潮遇三月 荒野遇山雀 我遇见你🐇",
        "📒银河遇初雪 我还没书写 你的消息🖋️",
        "💖光之于海底 火之于夏季 我之于你🐬",
        "☀️你是梦是雨 是我干渴的 白天夜里🌙",
    ],
}
// Beloved Title
const belovedTitle = {
    text1: 'Our',
    text2: 'Story'
}
// Beloved Timeline
const timeline = reactive({
    activities: {
        content: []
    },
    encryption: true, // true: 当前已被加密; false: 已解密
})
store.state.myAxios.get("Beloved").then((res) => {
    setTimeout(() => {
        timeline.activities.content = res.data.activities || [];
    }, 700);
});
const computed_acticities = computed(() => {
    if (timeline.encryption) {
        return timeline.activities.content.filter(
            (activity) => activity.type === "primary"
        );
    } else {
        return timeline.activities.content;
    }
})

</script>

<style lang="less" scoped>
#Beloved {
    position: relative;
    display: flex;
    justify-content: space-evenly;
    margin-top: 27px;
    transition: 0.7s;
}

#Beloved #bg {
    position: absolute;
    background: url(../assets/beloved/avatar_gif.gif);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: 0.7s;
    opacity: 0;
}

#Beloved #heart {
    position: absolute;
    background: url(../assets/beloved/heart.gif);
    width: 30%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 3;
    opacity: 0.9;
    transition: 0.7s;
}

#Beloved>img {
    width: 270px;
    height: 270px;
    border-radius: 50%;
    transition: 0.7s;
    position: relative;
    z-index: 2;
}

#Beloved #pyz {
    transform: rotateY(0.5turn);
}

#Beloved:hover #bg {
    opacity: 1;
}

#Beloved:hover #heart {
    opacity: 0;
}

#Beloved:hover>img {
    opacity: 0.85;
}

#Beloved:hover #pyz {
    transform: translateX(30%) rotateY(0.5turn);
}

#Beloved:hover #zyq {
    transform: translateX(-30%);
}

#timeline {
    position: relative;
    min-height: 300px;

    #Encryption {
        position: absolute;
        right: 0;
        top: 0;
        z-index: 1;
    }
}

#timeline /deep/ a {
    color: var(--main-blue) !important;
    font-weight: 700;
}

#timeline /deep/ .el-radio__label {
    color: var(--text);
}

#timeline /deep/ .el-timeline-item__tail {
    border-color: var(--main-blue) !important;
}

#timeline /deep/ .el-timeline-item__wrapper * {
    color: var(--text);
}
</style>